[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    background-color: var(--background);
    border-color: var(--color);
    color: inherit;
    height: 36px;
    padding: 6px 6px 6px 10px;
}

.select:focus, .select:active {
    border-color: var(--color);
}
